//项目中我们大多数时候都会把对新的接口请求封装成API来调用
import service from '@/service.js'
import getAxios from "@/getAxios"
import qs from 'qs'

//登录接口
export function login(data){
    //service执行完了 返回service回调的value
    return service({
        //想要传递的方式
        method:'post',
        //定义url
        url:'/login',
        //data为传进的数据
        data
    })
}

//学生列表接口
export function students(params){
    return service({
         method:'get',
         url:'/students',
        params
    })
}

//学生列表删除接口
export function studentDel(id){
    return service({
        method:'delete',
        //等价url:"/students/"+id,
        url:`/students/${id}`
    })
}

