//信息列表修改和新增接口
export function info(type,data){
    data=qs.stringify(data)
    let obj={
        method:type,url:'/info',
        data
    }
    return service(obj)
}


//信息列表查询接口
export function getInfo(){
    return service({
        method:'get',
        url:'/info',
    })
}

//信息列表删除接口
export function infoDel(id){
    return service({
        method:'delete',
        url:`info/${id}`
    })
}

//数据预览接口
export function dataview(){
    return service({
        method: 'get',
        url:'/dataview'
    })
}


//自定义get方法
export function get(url){
    return getAxios({
        method: 'get',
        url:url,
    })
}


//旅游地图接口
export function travel(){
    return service({
        method: 'get',
        url:'/travel'
    })
}